@font-face {
  font-family: "ptmono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "robotocondensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "ptmono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #666666;
}

header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: flex-start;
  width: 100%;
  background-color: transparent;
}
@media (min-width: 1024px) {
  header {
    padding: calc((100% - 73.4895%) / 2);
    padding-top: 33px;
    padding-bottom: 10px;
  }
}
@media (max-width: 1023px) {
  header {
    padding: calc((100% - 93.5065%) / 2);
    padding-top: 20px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  header {
    padding: calc((100% - 90.625%) / 2);
    padding-top: 17px;
    padding-bottom: 10px;
  }
}

img {
  display: block;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}

.modal-open {
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link,
.button {
  cursor: pointer;
}

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1420px;
}
@media (min-width: 1024px) {
  .container {
    width: 73.9583%;
  }
}
@media (max-width: 1023px) {
  .container {
    width: 77.1429%;
  }
}
@media (max-width: 767px) {
  .container {
    width: 90.625%;
  }
}
@media (min-width: 1024px) {
  .container--altered {
    width: 60.9375%;
  }
}
@media (max-width: 1023px) {
  .container--altered {
    width: 93.5065%;
  }
}

.logo {
  padding-right: 100px;
  margin-right: auto;
}
@media (max-width: 767px) {
  .logo--header img {
    width: 38px;
    height: 38px;
  }
}
.logo--footer {
  box-sizing: content-box;
  width: 216px;
}

