.ring-trigger {
  font-family: "robotocondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -0.08em;
  color: #ffffff;
  margin-top: 3px;
  padding: 15px 32px 12px 30px;
  border: 1px solid #ffffff;
  border-radius: 5px;
}
